import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { withPrefix } from 'gatsby-link'
import Header from '../components/header'
import Footer from '../components/footer'
import './index.css'

const Layout = ({ children, data }) => (
  <div>
    <Helmet
      title='Fitato&reg;: Best Gyms in Pune | One Pass to Fitness'
    >
      <script type="text/javascript">{`
        (function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
        new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
        j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
        'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
        })(window,document,'script','dataLayer','GTM-M6HNZB');
      `}</script>
      <meta name="description" content="Fitato&reg; is Your One Pass to 250+ Best Gyms & Fitness Studios in Pune. Access All Gyms, Dance, Yoga, Crossfit and Swimming Near You - One Pass @ â‚¹1199" />
      <link rel="icon" type="image/png" href={withPrefix('/static/fitato-favicon.png')} />
      <link href="https://fonts.googleapis.com/css?family=Roboto:400,400i,600,600i" rel="stylesheet" />
      <link rel="stylesheet" type="text/css" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css" />
    </Helmet>
    <noscript><iframe src="https://www.googletagmanager.com/ns.html?id=GTM-M6HNZB" height="0" width="0" style={{display: 'none', visibility: 'hidden'}}></iframe></noscript>
    <Header siteTitle={data.site.siteMetadata.title} />
    <div className="main-header-placeholder" />
    <div>
      {children()}
    </div>
    <Footer />
  </div>
)

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout

export const query = graphql`
  query SiteTitleQueryallAssets {
    site {
      siteMetadata {
        title
      }
    }
  }
`



